import React from 'react';
import { Row, Col, Card, Table, } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getGiftCardCategoriesSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getGiftCardCategories, deleteGiftCardCategories } from '../../api/payment/giftCards';
import GiftCardCategoriesFilter from '../../App/components/SearchFilters/GiftCardCategoriesFilter';
import { getStartEnd } from '../../helpers/getStartEnd';

const Discounts = (props) => {
    const [categoriesArr, setCategoriesArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, categoryId: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getGiftCardCategoriesSearchFilterObject(props),
            ...getStartEnd(props)
        }
        console.log(JSON.stringify(requestJson))
        getGiftCardCategories(requestJson, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setCategoriesArr(res.json.gift_card_categories);
                    setInstanceCount(res.json.count);
                    setLoading(false);
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });

        return () => controller.abort();
    }, []);


    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, categoryId: id });
    }

    const _delete = (idToBeDeleted) => {
        deleteGiftCardCategories({ action: 'delete', gift_card_categories: [idToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deleted = categoriesArr.find(instance => instance.id === idToBeDeleted);
                    let tempArr = categoriesArr;
                    tempArr.splice(tempArr.indexOf(deleted), 1);
                    await setCategoriesArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, categoryId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Gift Card Category successfully!', background: '#dffff0' })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const CategoriesList = categoriesArr.map((instance, index) => {
        return (
            <tr key={index}>
                <th scope="row">{instance.id}</th>
                <td
                    onClick={() => props.history.push('/gift-card-category/' + instance.id)}
                    style={{ cursor: 'pointer', color: '#55588b' }}
                >
                    {instance.name}
                </td>
                <td>{instance.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/gift-card-category/' + instance.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(instance.id)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Gift Card Categories</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`gift-card-category/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <GiftCardCategoriesFilter />
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {CategoriesList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(categoriesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.categoryId)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default Discounts;