import React from 'react';

import { Row, Col, Card, Table, Form, Collapse } from 'react-bootstrap';

import AsyncSelect from 'react-select/async';
import { withRouter } from 'react-router-dom';
import isEmpty from '../../../validation/isEmpty';
import { getItemCatagories } from '../../../api/inventory/itemCategory';
import Jhilikka from '../Reusables/Jhilikka';
import { getCustomers } from '../../../api/sales/customer';
import { getAccounts, getAccountTypes } from '../../../api/accounting/account';

import { getVendors } from '../../../api/inventory/vendorApi';
import { getGiftCardCategories } from '../../../api/payment/giftCards';

const filterDataModel = {
    name: '',
    enabled: '',
}

function GiftCardCategoriesFilter(props) {

    const [showFilters, setShowFilters] = React.useState(false);


    const [giftCardCategories, setGiftCardCategories] = React.useState([]);
    const [filterData, setFilterData] = React.useState({ ...filterDataModel });

    //extract the query params to fill the filter's form
    React.useEffect(() => {
        const queryString = props.location.search;
        const queryObj = new URLSearchParams(queryString) || '';
        let name = queryObj.get('name') || '';
        let enabled = queryObj.get('enabled') || '';

        setFilterData({
            ...filterData,
            name,
            enabled,
        })
    }, [])





    const _filterDataChange = (e) => {
        const { name, value, type } = e.target;
        setFilterData({
            ...filterData,
            [name]: (type === 'number' ? parseFloat(value) : value) || ''
        })
    }


    const _keypress = (e) => {
        if (e.key === 'Enter') {
            _search();
        }
    }


    const _search = () => {
        //append the query params
        let queryParams = new URLSearchParams();
        Object.keys(filterData).forEach(key => {
            if (filterData[key]) {
                queryParams.append(key, filterData[key])
            }
        });
        let queryString = queryParams.toString();

        props.history.replace({ search: queryString });
    }


    const _resetFilters = () => {
        setFilterData({ ...filterDataModel });
        props.history.replace();
    }

    //check if there are extra filters
    const NotificationDot = <div style={{
        background: "#a6c0ce",
        width: "12px",
        height: "12px",
        borderRadius: "50%",
        position: "relative",
        top: "-5px",
        left: "-7px",
    }}
        className="itemsSearchFilterNotificationDot"
    ></div>

    let showNotificationDot = false;
    for (let key in filterData) {
        if (key !== 'name') {
            if (!isEmpty((filterData[key] || '').toString())) {
                console.log(key)
                showNotificationDot = true;
            }
        }
    }

    return (
        <>
            <Card.Header className="filterInputs" onKeyDown={_keypress}>
                <div className="filterHeader" >
                    <div style={{ display: 'flex', flex: 9 }}>
                        <Form.Control
                            name="name"
                            value={filterData.name}
                            onChange={_filterDataChange}
                            size="sm"
                            type="text"
                            placeholder="Gift Card Name"
                            className="mainSearchBox"
                        />

                        <button
                            className="btn btn-secondary btn-sm"
                            onClick={_search}
                            style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                        > <i className="feather icon-search text-c-white f-8 mr-1" />Search</button>
                    </div>

                    <button
                        className="btn btn-secondary btn-sm showFilterButton"
                        onClick={() => setShowFilters(!showFilters)}

                    >
                        <div className="filterWrapper">
                            <i className="feather icon-sliders text-c-white f-8" />
                        </div>
                        <span>{showFilters ? "Hide" : "Show"} Filters
                        </span>

                    </button>
                    {showNotificationDot ? NotificationDot : null}

                </div>

                <Collapse in={showFilters}>
                    <div id="basic-collapse" >
                        <Card.Body className="filterBox pr-3 pl-3 pb-0">
                            <Row>
                                <Col md={3}>
                                    <Form.Label>Enabled</Form.Label>
                                    <Form.Control name='enabled' value={filterData.enabled} onChange={_filterDataChange} as="select">

                                        <option value='' >Not Selected</option>
                                        <option value='true'>Yes</option>
                                        <option value='false'>No</option>
                                    </Form.Control>
                                </Col>
                            </Row>
                            <br />

                            <Row style={{ display: 'flex', justifyContent: 'flex-end' }} >
                                <button
                                    className="btn btn-outline-secondary btn-sm mr-2"
                                    onClick={_resetFilters}
                                    style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                                >Reset Filters</button>
                                <button
                                    className="btn btn-secondary btn-sm mr-4"
                                    onClick={_search}
                                    style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                                >Apply Filters</button>

                            </Row>

                        </Card.Body>

                    </div>
                </Collapse>

            </Card.Header>
        </>
    )
}

export default withRouter(GiftCardCategoriesFilter);